<template>
  <div class="extraOrderDetail">
    <mt-header class="header">
      <a slot="left"  @click="back">
        <mt-button icon="back">护理信息</mt-button>
      </a>
      <mt-button slot="right" @click.native="showAllDate">{{date}}</mt-button>
    </mt-header>
    <div class="extraOrderDetailBodyHeader">
      <mt-navbar class="page-part" v-model="selected">
        <mt-tab-item id="baseData">基础数据</mt-tab-item>
        <mt-tab-item id="photoData">图文数据</mt-tab-item>
      </mt-navbar>
      <mt-tab-container v-model="selected">
        <mt-tab-container-item id="baseData">
          <div v-if="orderDetail.serviceType == 2 || orderDetail.serviceType == 3">
            <div class="dataItem" v-for="cd in commonData">
              <div class="dataItemHeader">
                <p>{{cd.title}}</p>
              </div>
              <div class="dataItemBody">
                <p v-for="cdCg in cd.catalog">
                  <span>{{cdCg.title}}</span>
                  <span>{{cdCg.value}}</span>
                </p>
              </div>
            </div>
            <div class="timsSlotData">
              <mt-navbar class="page-part" v-model="timeSelected">
                <mt-tab-item id="six">06:00-12:00</mt-tab-item>
                <mt-tab-item id="twelve">12:00-18:00</mt-tab-item>
                <mt-tab-item id="eightTeen">18:00-24:00</mt-tab-item>
                <mt-tab-item id="zero">00:00-06:00</mt-tab-item>
              </mt-navbar>
              <mt-tab-container v-model="timeSelected">
                <mt-tab-container-item id="six">
                  <div class="dataItem" v-for="sx in six">
                    <div class="dataItemHeader">
                      <p>{{sx.title}}</p>
                    </div>
                    <div class="dataItemBody">
                      <p v-for="sxCg in sx.catalog">
                        <span>{{sxCg.title}}</span>
                        <span>{{sxCg.value}}</span>
                      </p>
                    </div>
                  </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="twelve">
                  <div class="dataItem" v-for="tv in twelve">
                    <div class="dataItemHeader">
                      <p>{{tv.title}}</p>
                    </div>
                    <div class="dataItemBody">
                      <p v-for="tvCg in tv.catalog">
                        <span>{{tvCg.title}}</span>
                        <span>{{tvCg.value}}</span>
                      </p>
                    </div>
                  </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="eightTeen">
                  <div class="dataItem" v-for="en in eightTeen">
                    <div class="dataItemHeader">
                      <p>{{en.title}}</p>
                    </div>
                    <div class="dataItemBody">
                      <p v-for="enCg in en.catalog">
                        <span>{{enCg.title}}</span>
                        <span>{{enCg.value}}</span>
                      </p>
                    </div>
                  </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="zero">
                  <div class="dataItem" v-for="zo in zero">
                    <div class="dataItemHeader">
                      <p>{{zo.title}}</p>
                    </div>
                    <div class="dataItemBody">
                      <p v-for="zoCg in zo.catalog">
                        <span>{{zoCg.title}}</span>
                        <span>{{zoCg.value}}</span>
                      </p>
                    </div>
                  </div>
                </mt-tab-container-item>
              </mt-tab-container>
            </div>
          </div>
          <div v-else>
            <div class="dataItem" v-for="mom in momData">
              <div class="dataItemHeader">
                <p>{{mom.title}}</p>
              </div>
              <div class="dataItemBody">
                <p v-for="momCg in mom.catalog">
                  <span>{{momCg.title}}</span>
                  <span>{{momCg.value}}</span>
                </p>
              </div>
            </div>
            <div class="dataItem" v-for="infant in infantData">
              <div class="dataItemHeader">
                <p>{{infant.title}}</p>
              </div>
              <div class="dataItemBody">
                <p v-for="infantCg in infant.catalog">
                  <span>{{infantCg.title}}</span>
                  <span>{{infantCg.value}}</span>
                </p>
              </div>
            </div>
            <div class="dataItem" v-for="service in serviceData">
              <div class="dataItemHeader">
                <p>{{service.title}}</p>
              </div>
              <div class="dataItemBody">
                <p v-for="serviceCg in service.catalog">
                  <span class="doneCicle" v-if="serviceCg.value == 1"></span>
                  <span class="nodoneCicle" v-else></span>
                  <span>{{serviceCg.title}}</span>
                </p>
              </div>
            </div>
            <div class="dataItem" v-for="other in otherData">
              <div class="dataItemHeader">
                <p>{{other.title}}</p>
              </div>
              <div class="dataItemBody">
                <p v-for="otherCg in other.catalog">
                  <span>{{otherCg.title}}</span>
                  <span>{{otherCg.value}}</span>
                </p>
              </div>
            </div>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="photoData">
          <ul class="ivu-timeline">
            <li class="ivu-timeline-item" v-for="richText in richTexts">
              <div class="ivu-timeline-item-tail"></div>
              <div class="ivu-timeline-item-head ivu-timeline-item-head-blue"></div>
              <div class="ivu-timeline-item-content">
                <p class="time">{{richText.createdAt | timeFilter}}</p>
                <p class="header">{{richText.content}}</p>
                <div class="pt">
                  <img v-for="image in richText.images" :src="image" alt="">
                </div>
              </div>
            </li>
          </ul>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
    <mt-actionsheet :actions="actions" v-model="show" cancel-text=""></mt-actionsheet>
  </div>
</template>

<script>
import { mapGetters, mapActions} from 'vuex'
export default {
  name: 'extraOrderDetail',
  data () {
    return {
      show: false,
      actions: [],
      date: '',
      code: '',
      selected: 'baseData',
      richTexts: [],
      commonData: [],
      six: [],
      twelve: [],
      eightTeen:[],
      zero: [],
      timeSelected: 'six',
      momData:[],
      infantData: [],
      serviceData: [],
      otherData: []
    }
  },
  computed: {
    ...mapGetters({
      orderDetail:'orderDetail'
    })
  },
  methods: {
    ...mapActions({
      getExtraOrderDetail: 'getExtraOrderDetail'
    }),
    back: function() {
      history.go(-1);
    },
    showAllDate: function() {
      this.show = true;
      const ctx = this;
      let arr = [];
      for(let i = 0; i < this.orderDetail.serviceDuration ; i++) {
        let obj = {};
        const yeara = this.orderDetail.serviceDate.slice(0,4);
        const montha = this.orderDetail.serviceDate.slice(5,7);
        const datea = this.orderDetail.serviceDate.slice(8,10);
        const hours = this.orderDetail.serviceDate.slice(11,13)
        const minutes = this.orderDetail.serviceDate.slice(14,16);
        const stra = `${yeara}/${montha}/${datea} ${hours}:${minutes}`;
        let day = new Date(stra).getTime() + 86400000 * i;
        day = new Date(day);
        const year = day.getFullYear();
        const month = day.getMonth() + 1;
        const date = day.getDate();
        const str = year + '.' + month + '.' + date;
        obj['name'] = str;
        obj['method'] = function() {
          ctx.date = str;
        }
        arr.push(obj);
      }
      this.actions = arr;
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      const year = vm.orderDetail.serviceDate.slice(0,4);
      const month = vm.orderDetail.serviceDate.slice(5,7);
      const date = vm.orderDetail.serviceDate.slice(8,10);
      const str = year + '.' + month + '.' + date;
      vm.date = str;
      vm.code = to.query.code;
    })
  },
  watch: {
    date: function(val, oldVal) {
      const year = val.slice(0,4);
      const month = val.slice(5,7);
      const date = val.slice(8,10);
      let obj = {};
      obj.code = this.code;
      obj.date = year + '-' + month + '-' + date;
      this.getExtraOrderDetail(obj).then(response => {
          this.richTexts = response.data.richTexts;
          if(response.data.statistics && this.orderDetail.serviceType == 2) {
            this.commonData = [];
            this.six = [];
            this.twelve = [];
            this.eightTeen = [];
            this.zero = [];
            response.data.statistics = response.data.statistics ? response.data.statistics : [];
            response.data.statistics.forEach( i => {
              if(i.code == -1) {
                this.commonData.push(i);
              }
              if(i.code == 0) {
                this.six.push(i);
              }
              if(i.code == 1) {
                this.twelve.push(i);
              }
              if(i.code == 2) {
                this.eightTeen.push(i);
              }
              if(i.code == 3) {
                this.zero.push(i);
              }
              if(i.code == 5) {
                this.commonData.push(i);
              }
            })
          } else {
            this.momData = [];
            this.infantData = [];
            this.serviceData = [];
            this.otherData = [];
            response.data.statistics = response.data.statistics ? response.data.statistics : [];
            response.data.statistics.forEach( i => {
              if(i.code == 0) {
                this.momData.push(i);
              }
              if(i.code == 1) {
                this.infantData.push(i);
              }
              if(i.code == 2) {
                this.serviceData.push(i);
              }
              if(i.code == 5) {
                this.otherData.push(i);
              }
            })
          }
      })
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.extraOrderDetail {
  font-size: 0.4rem;
}
.extraOrderDetail .mint-actionsheet-listitem {
    font-size: 0.5rem;
    line-height: 1.2rem;
    height: 1.2rem;
}
.extraOrderDetail .mint-header-button.is-right {
  background: url(../assets/detailDown.png) no-repeat;
  background-position: right;
  padding-right: 0.3rem;
}
.dataItem {
  padding: 0.3rem;
}
.dataItemHeader p{
  height: 1rem;
  color: #d06091;
  line-height: 1rem;
}
.dataItemBody {
  display: flex;
  flex-wrap: wrap;
}
.dataItemBody p {
  line-height: 0.8rem;
  height: 0.8rem;
  width: 50%;
  color: #565256;
}
.dataItemBody p span {
  margin-right: 0.2rem;
}
.timsSlotData .dataItemBody p {
  width: 100%;
}
.extraOrderDetail .ivu-timeline {
    margin: 0;
    padding: 0.5rem 1rem;
    font-size: 0.4rem;
}
.extraOrderDetail .ivu-timeline, ol, ul {
    list-style: none;
}
.extraOrderDetail .ivu-timeline-item {
    margin: 0!important;
    padding: 0 0 12px;
    list-style: none;
    position: relative;
}
.extraOrderDetail .ivu-timeline-item-tail {
    height: 100%;
    border-left: 1px solid #d06091;
    position: absolute;
    left: 0.1rem;
    top: 0;
}
.extraOrderDetail .ivu-timeline-item-head-blue {
    border-color: #39f;
    color: #39f;
}
.extraOrderDetail .ivu-timeline-item-head {
    width: 0.2rem;
    height: 0.2rem;
    background-color: #d06091;
    border-radius: 50%;
    position: absolute;
}
.extraOrderDetail .ivu-timeline-item-content {
    padding: 1px 1px 10px 0.6rem;
    font-size: 0.4rem;
    position: relative;
    top: -3px;
}
.extraOrderDetail .time {
    font-size: 0.4rem;
    font-weight: 700;
    margin: 0;
    color: #d06091;
    margin-bottom: 0.3rem;
}
.extraOrderDetail .content {
    font-size: 0.4rem;
    font-weight: 700;
    margin: 0;
    color: #d06091;
    margin-bottom: 0.3rem;
}
.extraOrderDetail .ivu-article p {
    margin: 5px;
    font-size: 0.4rem;
}
.extraOrderDetail  .pt {
  display: flex;
  flex-wrap: wrap;
}
.extraOrderDetail .pt img {
  width: 2rem;
  height: 2rem;
  margin: 0 0.3rem 0.5rem 0;
}
.extraOrderDetail .mint-navbar .mint-tab-item.is-selected {
  border-bottom: 3px solid #d06091;
    color: #d06091;
    margin-bottom: -3px;
}
.extraOrderDetail .mint-actionsheet {
  max-height: 12rem;
}
.extraOrderDetail .doneCicle {
  display: inline-block;
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 0.2rem;
  border: 1px solid #d06091;
  background-color: #d06091;
}
.extraOrderDetail .nodoneCicle {
  display: inline-block;
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 0.2rem;
  border: 1px solid #d06091;
  background-color: #fff;
}
</style>



